@import 'local-fonts';

@font-face{
	font-family: 'Marck Script', cursive;
	src: url('../fonts/MarckScript-Regular.woff2') format('woff2'),
	url('../fonts/MarckScript-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}

@font-face{
	font-family: 'Roboto', sans-serif;
	src: url('../fonts/Roboto-Light.woff2') format('woff2'),
	url('../fonts/Roboto-Light.woff') format('woff');
	font-weight: 300;
	font-style: normal;
	font-display: swap;
}

@font-face{
	font-family: 'Roboto', sans-serif;
	src: url('../fonts/Roboto-Regular.woff2') format('woff2'),
	url('../fonts/Roboto-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?41b8u9');
  src:  url('../fonts/icomoon.eot?41b8u9#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?41b8u9') format('truetype'),
    url('../fonts/icomoon.woff?41b8u9') format('woff'),
    url('../fonts/icomoon.svg?41b8u9#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-cogs:before {
  content: "\f085";
}
.icon-gears:before {
  content: "\f085";
}
.icon-code:before {
  content: "\f121";
}

